body {
    background-image: linear-gradient(rgba(2, 3, 8, .94), rgba(2, 3, 8, .94)), url('./Assets/Images/movie_posters_background.jpg');
    margin-left: 72px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 959px) {
    body {
        margin-left: 0;
    }
}

/* Removes visual scrollbar at all times (Revisit this at a later point) */
::-webkit-scrollbar {
  -webkit-appearance: auto;
  width: 0;
}

.Mui-selected {
  /* border-left: 5px solid #2196f3 !important; */
  border-left: 5px solid red !important;
  /* color: rgb(190, 224, 255) !important; */
}

.MuiListItem-root.Mui-selected,
.MuiListItem-root.Mui-selected:hover {
  background-color: rgb(155 202 239 / 22%) !important;
}

.MuiInputBase-input {
  font-family: monospace !important;
  font-size: large !important;
}

.MuiPaper-elevation16 {
  background-color: #171d2e !important;
}

.wrap {
  overflow-x: hidden;
}

.slideInRight {
  animation-name: slideInRight;
}

.animated {
  animation-duration: 1.5s;
  animation-fill-mode: both;
}

@keyframes slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

